body {
  width: 100vw;
  height: 100vh;
  background: rgb(34, 193, 195);
  background: linear-gradient(
    22deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(253, 187, 45, 1) 100%
  );
  font-family: "Inclusive Sans";
  font-weight: 700;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main {
  background: rgb(63, 94, 251);
  background: linear-gradient(
    22deg,
    rgba(63, 94, 251, 1) 0%,
    rgba(0, 226, 252, 1) 100%
  );
  display: flex;
  flex-direction: column;
  padding: 15px;
  gap: 15px;
  /* height: 80vh; */
  height: fit-content;
  max-width: 20vw;
  border: 5px solid black;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgb(2, 0, 36);
  min-width: 40vw;
}
form {
  font-size: 2em;
  height: 7vh;
}
input {
  margin: 15px 0px;
  padding: 1px 10px;
  background: rgba(255, 255, 255, 0.21);
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4.7px);
  -webkit-backdrop-filter: blur(4.7px);
  border: 1px solid rgba(255, 255, 255, 1);
  font-size: 1.5rem;
}
input:focus {
  outline: none;
  background-color: cyan;
}
#go {
  cursor: pointer;
  padding: 2px 10px;
  font-size: 1.5rem;
  background: rgba(255, 255, 255, 0.21);
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4.7px);
  -webkit-backdrop-filter: blur(4.7px);
  border: 1px solid rgba(255, 255, 255, 1);
  transition: 0.1s ease-in;
}
#go:hover {
  transform: scale(1.05);
}
.maxAndminTempDiv {
  gap: 5px;
  display: flex;
  border-radius: 10px;
  height: 15vh;
  width: 100%;
  align-items: center;
}
.currentTempDiv,
.maxTempDiv,
.minTempDiv,
.windSpeed,
.description {
  color: black;
  font-size: 1.8em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;
  align-items: center;
  border-radius: 10px;
  height: 15vh;
  width: 99%;
  border: 1px solid black;
  transition: 0.1s ease-in;
  /* background: var(--cold); */
}
.minTempDiv{
  width: 90%;
}
.windSpeed{

  align-items: flex-start;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.currentTempDiv:hover,
.maxTempDiv:hover,
.minTempDiv:hover,
.windSpeed:hover,
.description:hover {
  cursor: pointer;
  transform: scale(1.02);
  background: rgba(255, 255, 255, 0.21);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4.7px);
  -webkit-backdrop-filter: blur(4.7px);
  border: 2px solid rgba(255, 255, 255, 1);
}
.error{
  font-size: 1.75em;
}
.loading{
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  height: 25%;
